const Jreact = {}
// 是因为Babel会转换jsx为 Jreact.createElement(tags, attrs, ...children)
Jreact.createElement = function(tag, attrs, ...children){
    console.log(arguments)
    return {
        tag,
        attrs,
        children
    }
}

function render (vnode, container) {
    if(typeof vnode ==='string') {
        return container.appendChild(document.createTextNode(vnode))
    }
    if(typeof vnode ==='object') {
        let dom = document.createElement(vnode.tag)
        setAttribute(dom, vnode.attrs)
        if(vnode.children&&Array.isArray(children)) {
            vnode.children.forEach(vnodeChild=>{
                render(vnodeChild, dom)
            })
        }
    }
    container.appendChild(dom)
}

function setAttribute(dom, attrs) {}

let name = "jirengu"
function clickBtn() {
    console.log('click me')
}

let vnode = (
                <div className="wrapper">
                    <h1>hello {name}</h1>
                    <button onClick={clickBtn}>click me</button>
                </div>)

console.log(vnode)
render(vnode,document.querySelector('#app'))